.bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    will-change: opacity;
}

button.home {
    background-color: rgba(255, 255, 255, 0.2);
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
    display: inline-block;
    width: 400px;
    position: relative;
    margin-left: 50px;
    padding: 20px;
    font-family: Courier;
    font-size: 20px;
    font-weight: 400;
    color: #ffffff;
}

div.fullscreen {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
}